import React, { useState, useEffect, useRef } from 'react';
import { Box, List, ListItem, Paper, CircularProgress, Typography, Button } from '@mui/material';
import { parseGIF, decompressFrames } from 'gifuct-js';
import CountdownOverlay from './CountdownOverlay';

interface GifFrameDisplayProps {
  gifPath: string;
  onFrameSelect?: (frameUrl: string, index: number) => void;
  onFramesUpdate?: (frames: string[]) => void;
  currentFrameIndex?: number;
  isAnimationEnabled?: boolean;
  setGifPath: (path: string) => void;
  onAnimationToggle?: (enabled: boolean) => void;
}

const GifFrameDisplay: React.FC<GifFrameDisplayProps> = ({ 
  gifPath, 
  onFrameSelect,
  onFramesUpdate,
  currentFrameIndex = -1,
  isAnimationEnabled = false,
  setGifPath,
  onAnimationToggle
}) => {
  const [frames, setFrames] = useState<string[]>([]);
  const [loading, setLoading] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);
  const selectedFrameRef = useRef<HTMLLIElement>(null);
  const containerRef = useRef<HTMLDivElement>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);

  // Auto-scroll to selected frame
  useEffect(() => {
    if (selectedFrameRef.current && containerRef.current && currentFrameIndex >= 0) {
      selectedFrameRef.current.scrollIntoView({
        behavior: 'smooth',
        block: 'center'
      });
    }
  }, [currentFrameIndex]);

  useEffect(() => {
    const loadGif = async () => {
      if (!gifPath) return;

      setLoading(true);
      setError(null);
      setFrames([]);

      try {
        const response = await fetch(gifPath);
        const buffer = await response.arrayBuffer();
        const gif = parseGIF(buffer);
        const frames = decompressFrames(gif, true);
        
        // Create canvas for frame rendering
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d');
        if (!ctx) throw new Error('Could not get canvas context');

        // Set canvas size to first frame dimensions
        canvas.width = frames[0].dims.width;
        canvas.height = frames[0].dims.height;

        // Convert frames to data URLs
        const frameUrls = frames.map(frame => {
          // Clear canvas
          ctx.clearRect(0, 0, canvas.width, canvas.height);

          // Create ImageData from pixel array
          const imageData = new ImageData(
            new Uint8ClampedArray(frame.patch),
            frame.dims.width,
            frame.dims.height
          );

          // Draw frame
          ctx.putImageData(imageData, frame.dims.left, frame.dims.top);

          // Convert to data URL
          return canvas.toDataURL('image/png');
        });

        setFrames(frameUrls);
        onFramesUpdate?.(frameUrls);
      } catch (err) {
        setError(err instanceof Error ? err.message : 'Failed to load GIF');
        console.error('Error loading GIF:', err);
      } finally {
        setLoading(false);
      }
    };

    loadGif();
  }, [gifPath, onFramesUpdate]);

  const handleFrameClick = (index: number, frameUrl: string) => {
    onFrameSelect?.(frameUrl, index);
  };

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        const result = e.target?.result as string;
        setGifPath(result);
        // Disable transitions after loading new slides
        onAnimationToggle?.(false);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleAnimationToggle = () => {
    onAnimationToggle?.(!isAnimationEnabled);
  };

  // Clean up blob URLs when component unmounts
  useEffect(() => {
    return () => {
      if (gifPath && gifPath.startsWith('blob:')) {
        URL.revokeObjectURL(gifPath);
      }
    };
  }, []);

  if (loading) {
    return (
      <Box sx={{ 
        height: '100%',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        backgroundColor: isAnimationEnabled ? '#1a332a' : '#111111'
      }}>
        <CircularProgress />
      </Box>
    );
  }

  if (error) {
    return (
      <Box sx={{ 
        height: '100%',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        backgroundColor: isAnimationEnabled ? '#1a332a' : '#111111',
        color: 'red'
      }}>
        {error}
      </Box>
    );
  }

  return (
    <div style={{ 
      height: '100%', 
      display: 'flex', 
      flexDirection: 'column',
      backgroundColor: isAnimationEnabled ? '#1a332a' : '#111111',
      transition: 'background-color 0.3s ease'
    }}>
      <div style={{ 
        padding: '20px', 
        display: 'flex', 
        flexDirection: 'column', 
        gap: '20px',
        backgroundColor: isAnimationEnabled ? '#1a332a' : '#111111',
        transition: 'background-color 0.3s ease'
      }}>
        <div style={{ 
          display: 'flex', 
          gap: '10px', 
          alignItems: 'center',
          backgroundColor: isAnimationEnabled ? '#1a332a' : '#111111',
          transition: 'background-color 0.3s ease'
        }}>
          <input
            type="file"
            accept="image/gif"
            onChange={handleFileChange}
            style={{ display: 'none' }}
            id="gif-upload"
          />
          <label htmlFor="gif-upload">
            <Button
              variant="contained"
              component="span"
              sx={{
                backgroundColor: isAnimationEnabled ? '#4CAF50' : '#1976d2',
                '&:hover': {
                  backgroundColor: isAnimationEnabled ? '#45a049' : '#1565c0',
                },
                transition: 'background-color 0.3s ease'
              }}
            >
              Load New Slides
            </Button>
          </label>
          <Button
            variant="contained"
            onClick={handleAnimationToggle}
            sx={{
              backgroundColor: isAnimationEnabled ? '#4CAF50' : '#1976d2',
              '&:hover': {
                backgroundColor: isAnimationEnabled ? '#45a049' : '#1565c0',
              },
              transition: 'background-color 0.3s ease'
            }}
          >
            {isAnimationEnabled ? 'Disable Transitions' : 'Enable Transitions'}
          </Button>
        </div>
      </div>
      <Box 
        ref={containerRef}
        sx={{ 
          height: '100%',
          backgroundColor: isAnimationEnabled ? '#1a332a' : '#111111',
          overflowY: 'auto',
          overflowX: 'hidden',
          padding: 2,
          boxSizing: 'border-box',
          transition: 'background-color 0.3s ease'
        }}
      >
        {!gifPath ? (
          <Box sx={{ 
            height: 'calc(100% - 60px)',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            color: 'white',
            backgroundColor: isAnimationEnabled ? '#1a332a' : '#111111'
          }}>
            <Typography>Select a GIF file to view frames</Typography>
          </Box>
        ) : (
          <List sx={{ 
            width: '100%',
            padding: 0,
            backgroundColor: isAnimationEnabled ? '#1a332a' : '#111111',
            transition: 'background-color 0.3s ease',
            '& > *:last-child': {
              marginBottom: 0
            }
          }}>
            {frames.map((frameUrl, index) => (
              <ListItem 
                key={index}
                ref={currentFrameIndex === index ? selectedFrameRef : undefined}
                sx={{ 
                  mb: 2,
                  p: 0,
                  width: '100%',
                  display: 'flex',
                  flexDirection: 'column',
                  alignItems: 'stretch',
                  cursor: 'pointer',
                  backgroundColor: isAnimationEnabled ? '#1a332a' : '#111111',
                }}
                onClick={() => handleFrameClick(index, frameUrl)}
              >
                <Paper 
                  sx={{ 
                    width: '100%',
                    p: 2,
                    backgroundColor: currentFrameIndex === index 
                      ? (isAnimationEnabled ? '#264d3d' : '#2c2c2c')
                      : '#1e1e1e',
                    color: 'white',
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    gap: 2,
                    border: currentFrameIndex === index ? '2px solid #4a90e2' : 'none',
                    transition: 'all 0.2s ease',
                    boxSizing: 'border-box',
                    '&:hover': {
                      backgroundColor: isAnimationEnabled ? '#264d3d' : '#2c2c2c',
                      transform: 'scale(1.01)'
                    }
                  }}
                >
                  <Typography variant="body2" sx={{ 
                    color: 'white',
                    width: '100%',
                    textAlign: 'center',
                    wordBreak: 'break-word',
                    fontSize: '0.8rem',
                    opacity: 0.8
                  }}>
                    Slide {index + 1} {currentFrameIndex === index ? '(Selected)' : ''}
                  </Typography>
                  <Box 
                    sx={{ 
                      width: '100%',
                      display: 'flex',
                      justifyContent: 'center',
                      borderRadius: 1,
                      overflow: 'hidden',
                      backgroundColor: '#000000',
                      position: 'relative'
                    }}
                  >
                    <img 
                      src={frameUrl} 
                      alt={`Frame ${index + 1}`}
                      style={{
                        width: '100%',
                        height: 'auto',
                        objectFit: 'contain',
                        maxHeight: '50vh'
                      }}
                    />
                    {currentFrameIndex === index && (
                      <CountdownOverlay 
                        isVisible={isAnimationEnabled}
                        intervalDuration={15000}
                      />
                    )}
                  </Box>
                </Paper>
              </ListItem>
            ))}
          </List>
        )}
      </Box>
    </div>
  );
};

export default GifFrameDisplay; 